import { ValidationOptions, registerDecorator } from "class-validator";
import { NameExistsRule } from "./name-exists.rule";

export function NameExists(model: any, validationOptions?: ValidationOptions) {
  return function (object: any, propertyName: string) {
    registerDecorator({
      name: 'NameExists',
      target: object.constructor,
      propertyName: propertyName,
      constraints: [model],
      options: validationOptions,
      validator: NameExistsRule,
    });
  };
}